import { useContext } from "react";
import {
  Header,
  GameSelector,
  VideoSection,
  BackgroundBlur,
  ChartSection,
} from "Components";
import { GameProvider, GameContext } from "GameContext";
import { default as HeatMap } from "./HeatMap/HeatMap";

const VideoAndChartsSection = () => {
  const {
    currentData: { columns },
  } = useContext(GameContext);
  return (
    <div
      style={
        columns && {
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }
      }
    >
      <VideoSection />
      <ChartSection />
    </div>
  );
};

function App() {
  return (
    <GameProvider>
      <BackgroundBlur />
      <Header />
      <GameSelector />
      <VideoAndChartsSection />
      <HeatMap />
    </GameProvider>
  );
}

export default App;
