import { useContext } from "react";
import styled from "styled-components";
import ReactPlayer from "react-player";

import { GameContext } from "GameContext";

const getVideoSection = columns => styled.div`
  display: flex;
  justify-content: space-between;
  padding: ${columns ? "1rem 0;" : "1rem 3.5rem;"}
    ${columns ? "flex-direction: column; height: 94vh;" : ""};
`;

const VideoView = ({ url }) => {
  return <ReactPlayer url={url} volume={0} autoplay muted loop playing />;
};

const VideoSection = () => {
  const {
    currentData: { streams, columns },
  } = useContext(GameContext);

  const VideoContainer = getVideoSection(columns);

  console.log({ streams });

  if (!streams) {
    return null;
  }

  return (
    <VideoContainer>
      {streams.map((url, idx) => (
        <VideoView url={url} key={`vid-${idx}`} />
      ))}
    </VideoContainer>
  );
};

export { VideoSection };
